import { login } from "./loginAction";
import instance from "../utils/config";
import { getUsers } from "./roleAction";

export const register = (formData) => async (dispatch) => {
  const config = {
    headers: {
      "Content-Type": "Application/json",
    },
  };

  try {
    formData.role = 0;
    const res = await instance.post("/api/users", formData, config);

    const newformData = {
      email: formData.email,
      password: formData.password,
    };
    dispatch(login(newformData));
  } catch (err) {
    console.error(err);
  }
};

export const addRole = (formData) => async (dispatch) => {
  const config = {
    headers: {
      "Content-Type": "Application/json",
    },
  };

  try {
    const res = await instance.post("/api/roles", formData, config);
    dispatch(getUsers());
  } catch (err) {
    console.error(err);
  }
};

export const updateRole = (formData, id) => async (dispatch) => {
  const config = {
    headers: {
      "Content-Type": "Application/json",
    },
  };

  try {
    const res = await instance.put(`/api/users/${id}`, formData, config);

    dispatch(getUsers());
  } catch (err) {
    console.error(err);
  }
};
