    const { name, checked, value } = e.target;
    let form_ = { ...form };
    let permissions = form_.permissions;
    if (checked) {
      permissions[name].push(value)
    } else {
      permissions[name] = permissions[name].filter((item) => item !== value);
    }
    setForm(form_);

