import { GET_ROLES, GET_CUSTOMERS } from "../types/roleTypes";
import instance from "../utils/config";

export const getUsers = () => async (dispatch) => {
  try {
    const res = await instance.get("/api/manageaccess");

    dispatch({ type: GET_ROLES, payload: res.data });
  } catch (err) {
    console.error(err);
  }
};
export const getCustomers = () => async (dispatch) => {
  try {
    const res = await instance.get("/api/manageaccess/customers");

    dispatch({ type: GET_CUSTOMERS, payload: res.data });
  } catch (err) {
    console.error(err);
  }
};

export const getUserById = (id) => async (dispatch) => {
  try {
    const res = await instance.get(`/api/users/${id}`);

    return res.data;
  } catch (err) {
    console.error(err);
  }
};

export const blockUser = (id, action) => async (dispatch) => {
  try {
    const res = await instance.put(`/api/users/block/${id}/${action}`);
    dispatch(getCustomers());
  } catch (err) {
    console.error(err);
  }
};

export const deleteUser = (id) => async (dispatch) => {
  try {
    const res = await instance.delete(`/api/users/${id}`);
    dispatch(getUsers());
    dispatch(getCustomers());
  } catch (err) {
    console.error(err);
  }
};
