import instance from "../../../utils/config";
import React, { useEffect, useState } from "react";
import ViewButton from "../buttons/ViewButton";
import { useNavigate, useParams } from "react-router-dom";
import SaveButton from "../buttons/SaveButton";

const AddUser = () => {
  const [userInfo, setUserInfo] = useState({ firstname: "", lastname: "", email: "", password: "", role: 0 });
  const [roles, setRoles] = useState([]);
  const params = useParams();
  const navigate = useNavigate();

  const handleChange = (e) => {
    setUserInfo({ ...userInfo, [e.target.name]: e.target.value })
  }

  useEffect(() => {
    if (params.id) {
      getUserByID();
    }
  }, [])

  const getUserByID = () => {
    instance.get(`/api/users/${params.id}`).then((res) => {
      const response = { ...res.data.data }
      setUserInfo({ ...userInfo, ...response })
    });
  }

  const getRoles = () => {
    instance.get("/api/manageaccess/roles").then((res) => {
      setRoles(res.data)
    });
  }

  const goBack = () => {
    navigate('/access');
  }
  useEffect(() => {
    getRoles()
  }, [])

  const submitHandler = async () => {
    const config = {
      headers: {
        "Content-Type": "Application/json",
      },
    };

    try {
      if (params.id) {
        const res = await instance.put(`/api/users/${params.id}`, userInfo, config);
        if (res) navigate('/access');
      } else {
        const res = await instance.post("/api/users", userInfo, config);
        if (res) navigate('/access');
      }
    } catch (err) {
      console.error(err);
    }
  }

  return (
    <>
      <div className="modal-container">
        <div className="modal-wrapper">
          <ViewButton
            handleClick={goBack}
            text="Go Back"
            className="go-back-btn"
          />
          <h3 className="modal-title">
            {params?.id ? 'View' : 'Add'} User
          </h3>
          <input
            name="firstname"
            type="text"
            placeholder="Enter first name"
            className="modal-input"
            value={userInfo?.firstname}
            onChange={handleChange}
          />
          <input
            name="lastname"
            type="text"
            placeholder="Enter last name"
            className="modal-input"
            value={userInfo?.lastname}
            onChange={handleChange}
          />
          <input
            name="email"
            type="email"
            placeholder="Enter email"
            className="modal-input"
            value={userInfo?.email}
            onChange={handleChange}
          />

          <input
            name="password"
            type="password"
            placeholder="Enter password"
            className="modal-input"
            value={userInfo?.password}
            onChange={handleChange}
          />

          <select name="role" onChange={handleChange} className="select-box" value={userInfo?.role} >
            <option>Select Role </option>
            {roles?.map(item => (
              <option value={item._id} >{item.roleTitle}</option>
            ))}
          </select>
          <SaveButton handleClick={submitHandler} />
        </div>

      </div>


    </>
  )
}

export default AddUser