import React, { useState, useEffect } from "react";
import "../../../styles/ManageAccess.css";
import AddButton from "../buttons/AddButton";
import RoleList from "./RoleList";
import { useNavigate } from "react-router-dom";
import instance from "../../../utils/config";
import ViewButton from "../buttons/ViewButton";
import DeleteButton from "../buttons/DeleteButton";
import { useDispatch, useSelector } from "react-redux";
import { getUserDetails } from "../../../actions/loginAction";

const Roles = () => {
  const [roles, setRoles] = useState([]);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const state = useSelector((state) => state);

  const handleClick = () => {
    navigate("/roles/add");
  }
  const handleDelete = (id) => {
    instance.delete(`/api/manageaccess/roles/${id}`).then((res) => {
      console.log(res);
      getRoles();
    });
  }
  useEffect(() => {
    getRoles();
  }, []);


  useEffect(() => {
    if (!state.userDetails) dispatch(getUserDetails());
  }, []);

  const getRoles = () => {
    instance.get("/api/manageaccess/roles").then((res) => {
      setRoles(res.data)
    });
  }

  return (
    <>
      <div className="access-container">
        <div className="access-section">
          <div className="access-section-header">
            <h3>All Roles</h3>
            {state.userDetails?.role?.permissions?.manageRole && state.userDetails?.role?.permissions?.manageRole?.includes("add") &&
              <AddButton resource="Role" handleClick={handleClick} />
            }
          </div>
          <table>
            <thead>
              <tr>
                <th>ID</th>
                <th>Name</th>
                <th>Options</th>
              </tr>
            </thead>

            <tbody>
              {roles?.map((item, index) => {
                return (
                  <tr key={item._id}>
                    <td>{index + 1}</td>
                    <td>{item.roleTitle}</td>
                    <td>
                      <div className="button-group">
                        {state.userDetails?.role?.permissions?.manageRole && state.userDetails?.role?.permissions?.manageRole?.includes("edit") &&
                          <ViewButton
                            text="Edit"
                            handleClick={() => navigate(`/roles/edit/${item._id}`)}
                          />
                        }
                        {state.userDetails?.role?.permissions?.manageRole && state.userDetails?.role?.permissions?.manageRole?.includes("delete") &&
                          <DeleteButton
                            text="Delete"
                            handleClick={() => handleDelete(item?._id)}
                          />
                        }
                      </div>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>
    </>
  )
}

export default Roles