import React, { useEffect } from "react";
import "../../../styles/News.css";
import AddButton from "../buttons/AddButton";
import NewsList from "./NewsList";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { getUserDetails } from "../../../actions/loginAction";

const NewsSection = () => {
  const navigate = useNavigate();
  const handleClick = () => {
    navigate("/news/modal");
  };
  const dispatch = useDispatch();
  const state = useSelector((state) => state);
  useEffect(() => {
    if (!state.userDetails) dispatch(getUserDetails());
  }, []);
  return (
    <div className="news-container">
      <div className="news-header">
        <h3>News</h3>
        {state?.userDetails?.role?.permissions?.news && state.userDetails?.role?.permissions?.news?.includes("add") &&
          <AddButton resource="News" handleClick={handleClick} />
        }
      </div>
      <NewsList rolePermissions={state?.userDetails} />
    </div>
  );
};

export default NewsSection;
