import React, { useState } from "react";
import '../../../styles/NewsLetter.css';
import FilterBar from "../FilterBar";

const NewsletterList = ({ newsletter }) => {

  const filterOptions = [
    {
      title: "Email",
      value: "email",
    }
  ];

  const [filterValue, setFilterValue] = useState(filterOptions[0].value);
  const [filterText, setFilterText] = useState("");

  if (filterText) {
    newsletter = newsletter.filter((item) =>
      item[filterValue]?.toLowerCase().includes(filterText)
    );
  }
  
  return (
    <>
    <FilterBar
        filterOptions={filterOptions}
        filterValue={filterValue}
        filterText={filterText}
        setFilterText={setFilterText}
        setFilterValue={setFilterValue}
      />
      <table>
        <thead>
              <tr>
              <th>S.No.</th>
              <th>Email</th>
            </tr>
        </thead>

        <tbody>
        {newsletter?.slice(0).reverse().map((item, index) => {
            return (
              <tr key={item?._id}>
                <td>{index + 1}</td>
                <td>{item?.email}</td>
              </tr>
              )
            })}
        </tbody>
      </table>
    </>
  );
};

export default NewsletterList;
