import { GET_NEWS, ADD_NEWS, GET_SINGLE_NEWS } from "../types/newsTypes";

export const newsReducer = (state = null, action) => {
  switch (action.type) {
    case GET_NEWS:
      return { ...state, news: action.payload };
    case GET_SINGLE_NEWS:
      return { ...state, news_detail: action.payload };
    default:
      return state;
  }
};
