import React, { useEffect } from "react";
import "../../styles/Dashboard.css";
import LeftNavbar from "../../components/ui/LeftNavbar";
import DashboardWindow from "../../components/ui/dashboard/DashboardWindow";
import { useDispatch, useSelector } from "react-redux";
import { getUserDetails } from "../../actions/loginAction";

const Mainscreen = () => {
  const dispatch = useDispatch();

  const state = useSelector((state) => state);

  useEffect(() => {
    if (!state.userDetails) dispatch(getUserDetails());
  }, []);

  const adminLinks = [
    {
      title: "Dashboard",
      route: "/",
    },
  ];

  if (state.userDetails?.role?.permissions?.customerDetail && state.userDetails?.role?.permissions?.customerDetail?.includes("view"))
    adminLinks.push({
      title: "Customer Details",
      route: "/customerdetails",
    });

    if (state.userDetails?.role?.permissions?.manageRole && state.userDetails?.role?.permissions?.manageRole?.includes("view"))
    adminLinks.push({
      title: "Manage Role",
      route: "/roles",
    });
    if (state.userDetails?.role?.permissions?.manageUsers && state.userDetails?.role?.permissions?.manageUsers?.includes("view"))
    adminLinks.push({
      title: "Users",
      route: "/access",
    });
    if (state.userDetails?.role?.permissions?.categories && state.userDetails?.role?.permissions?.categories?.includes("view"))
    adminLinks.push({
      title: "Categories",
      route: "/categories",
    });
    if (state.userDetails?.role?.permissions?.news && state.userDetails?.role?.permissions?.news?.includes("view"))
    adminLinks.push({
      title: "News",
      route: "/news",
    });
    if (state.userDetails?.role?.permissions?.newsApproval && state.userDetails?.role?.permissions?.newsApproval?.includes("view"))
    adminLinks.push({
      title: "News Approval",
      route: "/usernews",
    });
    if (state.userDetails?.role?.permissions?.ratings && state.userDetails?.role?.permissions?.ratings?.includes("view"))
    adminLinks.push({
      title: "Ratings",
      route: "/ratings",
    });
    if (state.userDetails?.role?.permissions?.channels && state.userDetails?.role?.permissions?.channels?.includes("view"))
    adminLinks.push({
      title: "Channels",
      route: "/channels",
    });
    if (state.userDetails?.role?.permissions?.memberships && state.userDetails?.role?.permissions?.memberships?.includes("view"))
    adminLinks.push({
      title: "Memberships",
      route: "/memberships",
    });
    adminLinks.push({
      title: "Advertisements",
      route: "/advertisements/modal",
    });
    adminLinks.push({
      title: "Newsletter",
      route: "/newsletter",
    });
    if (state.userDetails?.role?.permissions?.settings && state.userDetails?.role?.permissions?.settings?.includes("view"))
    adminLinks.push({
      title: "Settings",
      route: "/settings",
    });
 

  return (
    <div className="main-window">
      <LeftNavbar links={adminLinks} />
      <DashboardWindow />
    </div>
  );
};

export default Mainscreen;
