import React, { useState, useEffect } from "react";
import "../../../styles/Modal.css";
import { useDispatch, useSelector } from "react-redux";
import { getNewsById } from "../../../actions/newsAction";
import ViewButton from "../buttons/ViewButton";
import { useNavigate, useParams } from "react-router-dom";

const ViewNewsModal = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const params = useParams();

  const storedState = useSelector((state) => state);

  useEffect(() => {
    dispatch(getNewsById(params?.id));
  }, []);


  const closeModal = () => {
    // dispatch({ type: CLOSE_MODAL, payload: null });
    navigate("/news");
  };

  return (
    <div className="modal-container">
      <div className="modal-wrapper">
        <ViewButton
          handleClick={closeModal}
          text="Go Back"
          className="go-back-btn"
        />

        <div className="news-modal-content">
          <h3 className="modal-title">{storedState?.news?.news_detail?.title}</h3>
          <img
            src="https://us.123rf.com/450wm/alhovik/alhovik1709/alhovik170900031/86481591-breaking-news-background-world-global-tv-banner-pubblicitari.jpg?ver=6"
            alt=""
          />

          <div className="news-content-item">
            <h4>Author</h4>
            <p>{storedState?.news?.news_detail?.author}</p>
          </div>

          <div className="news-content-item">
            <h4>Category</h4>
            <p>{storedState?.news?.news_detail?.category?.value}</p>
          </div>

          <img
            src="https://us.123rf.com/450wm/alhovik/alhovik1709/alhovik170900031/86481591-breaking-news-background-world-global-tv-banner-pubblicitari.jpg?ver=6"
            alt=""
          />

          <div className="news-content-item description-news-item">
            <h4>Description 1</h4>
            <p>{storedState?.news?.news_detail?.desc}</p>
          </div>

          <div className="news-content-item">
            <h4>Description 2</h4>
            <p dangerouslySetInnerHTML={{ __html: storedState?.news?.news_detail?.desc2 }}></p>
          </div>

          <div className="news-content-item">
            <h4>Country</h4>
            <p>{storedState?.news?.news_detail?.country}</p>
          </div>

          <div className="news-content-item">
            <h4>State</h4>
            <p>{storedState?.news?.news_detail?.state}</p>
          </div>

          <div className="news-content-item">
            <h4>City</h4>
            <p>{storedState?.news?.news_detail?.city}</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ViewNewsModal;
