import React, { useState, useEffect } from "react";
import "../../../styles/Modal.css";
import { useDispatch } from "react-redux";
import { CLOSE_MODAL } from "../../../types/modalTypes";
import {
  addMemberships,
  fetchMembershipById,
  updateMembership,
} from "../../../actions/membershipsAction";
import SaveButton from "../buttons/SaveButton";
import ViewButton from "../buttons/ViewButton";
import { useNavigate, useParams } from "react-router-dom";

const MembershipsModal = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const params = useParams();
  const [form, setForm] = useState({ name: '', monthly_price: '', annually_price: '', permissions: { post: [], postCount: '', myProfile: [], channel: [], stock: [] }});
  const [updateData, setUpdateData] = useState(false);
  const {
    name,
    monthly_price,
    annually_price,
    canComment,
    canDeletePost,
    viewPost,
  } = form;

  useEffect(() => {
    const fetchMembership = async () => {
      const membership = await dispatch(fetchMembershipById(params.id));

      setForm(membership);
    };

    if (params.id) {
      fetchMembership();
      setUpdateData(true);
    }
  }, []);

  const handleChange = (e) => {
    const form_ = { ...form };
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleChangeSecond = (e) => {
     setForm({ ...form, ...{permissions: {...form.permissions, [e.target.name]: e.target.value }}})
  };
  const handleCheckboxChange = (e) => {
     const { name, checked, value } = e.target;
     let form_ = { ...form };
     let permissions = form_.permissions;
     if (checked) {
       permissions[name].push(value);
     } else {
       permissions[name] = permissions[name].filter((item) => item !== value);
     }
     setForm(form_);
  };
  const handleSubmit = async (e) => {
    e.preventDefault();
    if (updateData === false) {
      dispatch(addMemberships(form));
    } else {
      dispatch(updateMembership(form, params.id));
    }
    dispatch({ type: CLOSE_MODAL });
    navigate("/memberships");
  };
  const closeModal = () => {
    // dispatch({ type: CLOSE_MODAL, payload: null });
    navigate("/memberships");
  };

  return (
    <div className="modal-container">
      <div className="modal-wrapper">
        <ViewButton
          handleClick={closeModal}
          text="Go Back"
          className="go-back-btn"
        />
        <h3 className="modal-title">
          {updateData === false ? "Add" : "Update"} Memberships
        </h3>
        <input
          name="name"
          type="text"
          placeholder="Plan name"
          className="modal-input"
          value={name}
          onChange={handleChange}
        />
        <input
          name="monthly_price"
          type="number"
          placeholder="Plan monthly price"
          className="modal-input"
          value={monthly_price}
          onChange={handleChange}
        />
        <input
          name="annually_price"
          type="number"
          placeholder="Plan annually price"
          className="modal-input"
          value={annually_price}
          onChange={handleChange}
        />
        <div className="main-div">
          <table class="table table-bordered">
            <thead>
              <tr>
                <th scope="col">Sr.No.</th>
                <th scope="col">Permission</th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td colSpan={1}>1</td>
                <td colSpan={1}>Post</td>
                <td colSpan={2}>
                  <div className="checkbox-group">
                    <input
                      type="checkbox"
                      name="post"
                      onChange={handleCheckboxChange}
                      value='add_post'
                      checked={form?.permissions?.post?.includes('add_post')}
                    />
                    <label htmlFor="">Add Post</label>
                  </div>
                </td>
                <td colSpan={2}>
                  <div className="checkbox-group">
                    <input type="text" name="postCount" value={form?.permissions?.postCount} onChange={handleChangeSecond} placeholder="Enter Add Post Count" style={{ width: '100%'}}/>
                  </div>
                </td>
                <td colSpan={2}>
                  <div className="checkbox-group">
                    <input
                      type="checkbox"
                      name="post"
                      onChange={handleCheckboxChange}
                      value="view_post_detail"
                      checked={form?.permissions?.post?.includes('view_post_detail')}
                    />
                    <label htmlFor="">View Post</label>
                  </div>
                </td>
                <td colSpan={2}>
                  <div className="checkbox-group">
                    <input
                      type="checkbox"
                      name="post"
                      onChange={handleCheckboxChange}
                      value="edit_post"
                      checked={form?.permissions?.post?.includes('edit_post')}
                    />
                    <label htmlFor="">Post Edit</label>
                  </div>
                </td>
                <td colSpan={2}>
                  <div className="checkbox-group">
                    <input
                      type="checkbox"
                      name="post"
                      onChange={handleCheckboxChange}
                      value="delete_post"
                      checked={form?.permissions?.post?.includes('delete_post')}
                    />
                    <label htmlFor="">Post Delete</label>
                  </div>
                </td>
                <td colSpan={2}>
                  <div className="checkbox-group">
                    <input
                      type="checkbox"
                      name="post"
                      onChange={handleCheckboxChange}
                      value="comment_post"
                      checked={form?.permissions?.post?.includes('comment_post')}
                    />
                    <label htmlFor="">Post Comment</label>
                  </div>
                </td>
                <td colSpan={2}>
                  <div className="checkbox-group">
                    <input
                      type="checkbox"
                      name="post"
                      onChange={handleCheckboxChange}
                      value="like_post"
                      checked={form?.permissions?.post?.includes('like_post')}
                    />
                    <label htmlFor="">Like Post</label>
                  </div>
                </td>
                <td colSpan={2}>
                  <div className="checkbox-group">
                    <input
                      type="checkbox"
                      name="post"
                      onChange={handleCheckboxChange}
                      value="share_post"
                      checked={form?.permissions?.post?.includes('share_post')}
                    />
                    <label htmlFor="">Share Post</label>
                  </div>
                </td>
                <td colSpan={2}>
                  <div className="checkbox-group">
                    <input
                      type="checkbox"
                      name="post"
                      onChange={handleCheckboxChange}
                      value="see_like_count"
                      checked={form?.permissions?.post?.includes('see_like_count')}
                    />
                    <label htmlFor="">See Like Count</label>
                  </div>
                </td>
                <td colSpan={2}>
                  <div className="checkbox-group">
                    <input
                      type="checkbox"
                      name="post"
                      onChange={handleCheckboxChange}
                      value="see_comment_count"
                      checked={form?.permissions?.post?.includes('see_comment_count')}
                    />
                    <label htmlFor="">See Comment Count</label>
                  </div>
                </td>
                <td colSpan={2}>
                  <div className="checkbox-group">
                    <input
                      type="checkbox"
                      name="post"
                      onChange={handleCheckboxChange}
                      value="see_share_count"
                      checked={form?.permissions?.post?.includes('see_share_count')}
                    />
                    <label htmlFor="">See Share Count</label>
                  </div>
                </td>
              </tr>
              <tr>
                <td colSpan={1}>2</td>
                <td colSpan={1}>My Profile</td>
                <td colSpan={2}>
                  <div className="checkbox-group">
                    <input
                      type="checkbox"
                      name="myProfile"
                      onChange={handleCheckboxChange}
                      value='view_all_post'
                      checked={form?.permissions?.myProfile?.includes('view_all_post')}
                    />
                    <label htmlFor="">View All Post</label>
                  </div>
                </td>
                <td colSpan={2}>
                  <div className="checkbox-group">
                    <input
                      type="checkbox"
                      name="myProfile"
                      onChange={handleCheckboxChange}
                      value='view_videos'
                      checked={form?.permissions?.myProfile?.includes('view_videos')}
                    />
                    <label htmlFor="">View Videos</label>
                  </div>
                </td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
              </tr>
              <tr>
                <td colSpan={1}>3</td>
                <td colSpan={1}>Channel</td>
                <td colSpan={2}>
                  <div className="checkbox-group">
                    <input
                      type="checkbox"
                      name="channel"
                      onChange={handleCheckboxChange}
                      value='view_channel'
                      checked={form?.permissions?.channel?.includes('view_channel')}
                    />
                    <label htmlFor="">View Channel</label>
                  </div>
                </td>
                <td colSpan={2}>
                  <div className="checkbox-group">
                    <input
                      type="checkbox"
                      name="channel"
                      onChange={handleCheckboxChange}
                      value='add_channel'
                      checked={form?.permissions?.channel?.includes('add_channel')}
                    />
                    <label htmlFor="">Add Channel</label>
                  </div>
                </td>
                <td colSpan={2}>
                  <div className="checkbox-group">
                    <input
                      type="checkbox"
                      name="channel"
                      onChange={handleCheckboxChange}
                      value='edit_channel'
                      checked={form?.permissions?.channel?.includes('edit_channel')}
                    />
                    <label htmlFor="">Edit Channel</label>
                  </div>
                </td>
                <td colSpan={2}>
                  <div className="checkbox-group">
                    <input
                      type="checkbox"
                      name="channel"
                      onChange={handleCheckboxChange}
                      value='delete_channel'
                      checked={form?.permissions?.channel?.includes('delete_channel')}
                    />
                    <label htmlFor="">Delete Channel</label>
                  </div>
                </td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
              </tr>
              <tr>
                <td colSpan={1}>4</td>
                <td colSpan={1}>Stock</td>
                <td colSpan={2}>
                  <div className="checkbox-group">
                    <input
                      type="checkbox"
                      name="stock"
                      onChange={handleCheckboxChange}
                      value='view_stock'
                      checked={form?.permissions?.stock?.includes('view_stock')}
                    />
                    <label htmlFor="">View Stock</label>
                  </div>
                </td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
                <td colSpan={2}></td>
              </tr>
            </tbody>
          </table>
        </div>

        <SaveButton handleClick={handleSubmit} />
      </div>
    </div>
  );
};

export default MembershipsModal;
