import React, { useEffect } from "react";
import "../../../styles/Categories.css";
import "../../../styles/Modal.css";
import CategoriesList from "./CategoriesList";
import AddButton from "../buttons/AddButton";
import { useDispatch, useSelector } from "react-redux";
import { fetchCategories } from "../../../actions/categoryActions";
import { useNavigate } from "react-router-dom";
import { getUserDetails } from "../../../actions/loginAction";

const CategoriesSection = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const state = useSelector((state) => state);

  useEffect(() => {
    if (!state.categories) dispatch(fetchCategories());
    if (!state.userDetails) dispatch(getUserDetails());
  }, []);

  const handleClick = () => {
    navigate("/categories/modal");
  };

  
  return (
    <div className="categories-container">
      <div className="categories-list">
        <div className="categories-list-header">
          <h3>Categories</h3>
          {state.userDetails?.role?.permissions?.categories && state.userDetails?.role?.permissions?.categories?.includes("add") &&
            <AddButton resource="Category" handleClick={handleClick} />
          }
        </div>

        <CategoriesList categories={state?.categories} rolePermissions={state?.userDetails} />
      </div>
    </div>
  );
};

export default CategoriesSection;
