import React, { useState, useEffect } from "react";
import "../../../styles/Modal.css";
import ViewButton from "../buttons/ViewButton";
import { useNavigate, useParams } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { getNewsById } from "../../../actions/newsAction";

const UserCommentsModal = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const params = useParams();
  const state = useSelector((state) => state);

  useEffect(() => {
    if (!state?.news?.news) dispatch(getNewsById(params?.id));
  }, [state?.news?.news]);

  let Info = state?.news;
  console.log('Info', Info?.news);

  const closeModal = () => {
    // dispatch({ type: CLOSE_MODAL, payload: null });
    navigate("/ratings");
  };

  return (
    <div className="modal-container">
      <div className="modal-wrapper">
        <ViewButton
          handleClick={closeModal}
          text="Go Back"
          className="go-back-btn"
        />
        <div className="news-modal-content comments-modal">
        <h3 className="modal-title">Comments</h3>
          {Info?.news?.comments?.map((item, index) => {
            return (
              <div className="card mb-13">
                <div className="card-body">
                  <div className="user">
                    <p className="mt-0"><b>User :</b> {item?.user?.firstname} {item?.user?.lastname}</p>
                    <p className="mt-0"></p>
                  </div>
                  <div className="comment">
                    <p className="mb-0"><b>Comment :</b> {item?.text}</p>
                  </div>
                </div>
              </div>
            )
          })}
        </div>
      </div>
    </div>
  );
};

export default UserCommentsModal;
