import React, { useEffect } from "react";
import { Routes, Route, useNavigate } from "react-router-dom";
import TopNavbar from "../TopNavbar";
import DashboardSection from "./DashboardSection";
import ManageAccessSection from "../access/ManageAccessSection";
import CategoriesSection from "../categories/CategoriesSection";
import CustomerDetailsSection from "../customerdetails/CustomerDetailsSection";
import NewsSection from "../news/NewsSection";
import UserNewsSection from "../usernews/UserNewsSection";
import MembershipsSection from "../memberships/MembershipsSection";
import AdvertisementsModal from "../advertisements/AdvertisementsModal";
import ChannelsSection from "../channels/ChannelsSection";
import RatingsSection from "../ratings/RatingsSection";
import SettingsSection from "../settings/SettingsSection";
import "../../../styles/Dashboard.css";
import Toast from "../../ui/Toast";
import AccessModal from "../modals/AccessModal";
import CategoriesModal from "../modals/CategoriesModal";
import NewsModal from "../modals/NewsModal";
import ViewNewsModal from "../modals/ViewNewsModal";
import ViewUserNewsModal from "../modals/ViewUserNewsModal";
import CustomerDetailsModal from "../modals/CustomerDetailsModal";
import ViewCustomerDetailsModal from "../modals/ViewCustomerDetailsModal";
import NewsletterSection from '../newsletter/NewsletterSection';
import UserCommentsModal from "../modals/UserCommentsModal";

import RolesModal from "../access/Roles";
import AddUserModal from "../access/AddUser";

import { useDispatch, useSelector } from "react-redux";
import MembershipsModal from "../modals/MembershipsModal";

const DashboardWindow = () => {
  const navigate = useNavigate();
  const state = useSelector((state) => state);

  return (
    <div className="main-window-right">
      <TopNavbar />
      <div className="main-window-right-bottom">
        <Routes>
          <Route exact path="/" element={<DashboardSection />} />
          <Route exact path="/access" element={<ManageAccessSection />} />
          <Route exact path="/roles" element={<RolesModal />} />
          <Route exact path="/categories" element={<CategoriesSection />} />
          <Route
            exact
            path="/customerdetails"
            element={<CustomerDetailsSection />}
          />
          <Route exact path="/news" element={<NewsSection />} />
          <Route exact path="/usernews" element={<UserNewsSection />} />
          <Route exact path="/memberships" element={<MembershipsSection />} />
          <Route exact path="/channels" element={<ChannelsSection />} />
          <Route exact path="/ratings" element={<RatingsSection />} />
          <Route exact path="/settings" element={<SettingsSection />} />
          <Route exact path="/newsletter" element={<NewsletterSection />} />

          {/* Modals */}
          <Route
            exact
            path="/customerdetails/modal"
            element={<CustomerDetailsModal />}
          />
          <Route
            exact
            path="/customerdetails/modal/:id"
            element={<CustomerDetailsModal />}
          />
          <Route exact path="/access/modal" element={<AddUserModal />} />
          <Route exact path="/access/modal/:id" element={<AddUserModal />} />
      
          <Route exact path="/roles/add" element={<AccessModal />} />
          <Route exact path="/roles/edit/:id" element={<AccessModal />} />
          <Route exact path="/categories/modal" element={<CategoriesModal />} />
          <Route
            exact
            path="/categories/modal/:id"
            element={<CategoriesModal />}
          />
          <Route exact path="/news/modal" element={<NewsModal />} />
          <Route exact path="/news/modal/:id" element={<NewsModal />} />
          <Route exact path="/news/view/:id" element={<ViewNewsModal />} />
          <Route exact path="/ratings/comments/:id" element={<UserCommentsModal />} />
          <Route
            exact
            path="/usernews/view/:id"
            element={<ViewUserNewsModal />}
          />
          <Route
            exact
            path="/customerdetails/view/:id"
            element={<ViewCustomerDetailsModal />}
          />
          <Route
            exact
            path="/memberships/modal"
            element={<MembershipsModal />}
          />
          <Route
            exact
            path="/memberships/modal/:id"
            element={<MembershipsModal />}
          />
          <Route exact path="/advertisements/modal" element={<AdvertisementsModal />} />
        </Routes>
      </div>
      {state.modal !== null ? (
        <Toast
          title={state.modal.title}
          handleClick={state.modal.handleClick}
        />
      ) : null}
    </div>
  );
};

export default DashboardWindow;
