import { GET_NEWSLETTER } from '../types/newsletterTypes';
import instance from '../utils/config';

export const fecthNewsletters = () => async (dispatch) => {
  try {
    const res = await instance.get('/api/newsletter');
   
    dispatch({ type: GET_NEWSLETTER, payload: res.data });
  } catch (err) {
    console.error(err);
  }
};